








































































































.none {
  text-align: center;
  margin-top: 30%;
  .weui-icon-safe-warn {
    font-size: 50px;
  }
  p {
    font-size: 16px;
    margin-top: 10px;
    color: #999;
  }
}

.list {
    padding-bottom: 100px;
  .address {
    background-color: #fff;
    margin-top: 10px;
    padding: 8px 5px 30px 5px;
    .head {
      width: 100%;
      font-weight: 700;
      font-size: 16px;
      overflow: hidden;
      line-height: 30px;
    }
    .content {
      display: block;
      position: relative;
      img {
        position: absolute;
        left: 0;
        top: 5px;
        width: 15px;
        height: 15px;
      }
      .detail {
        padding-left: 30px;
        font-size: 14px;
        color: #666;
      }
    }
    .bottom-address {
      position: relative;
      border-top: 1px solid #f2f2f2;
      padding-top: 10px;
      .item-left {
        float: left;
        position: relative;
        font-size: 14px;
        color: #666;
        img {
          position: absolute;
          width: 20px;
          height: 20px;
        }
        span {
          padding-left: 25px;
        }
      }
      .item-right {
        float: right;
        position: relative;
        font-size: 14px;
        color: #666;
        margin-left: 30px;
        img {
          position: absolute;
          top: 2px;
          width: 15px;
          height: 15px;
        }
        span {
          padding-left: 20px;
        }
      }
    }
  }
  .btn-address {
    position: absolute;
    margin-top: 40px;
    margin-bottom: 200px;
    left: 10%;
    right: 10%;
    height: 48px;
    background-color: #343434;
    text-align: center;
    line-height: 48px;
    color: #eed496;
    border-radius: 10px;
  }
}



