@import url('../assets/base.css');
.create-indent {
  margin-bottom: 100px;
}
.create-indent .flex {
  display: flex;
  display: -webkit-flex;
}
.create-indent .eclipse-2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
}
.create-indent .address {
  background-color: #fff;
  position: relative;
  padding-right: 30px;
}
.create-indent .address .icon-arrow {
  position: absolute;
  right: 5px;
  top: 30%;
  color: #666;
}
.create-indent .address .address-heade {
  padding: 5px;
  height: 30px;
  line-height: 30px;
}
.create-indent .address .address-heade span {
  font-family: PingFangSC-Semibold, sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #333;
}
.create-indent .address .address-heade span .iconfont {
  font-size: 12px;
  margin-right: 5px;
}
.create-indent .address .address-heade span .icon-shouhuorenshoujihao {
  margin-left: 10px;
}
.create-indent .address .address-detail {
  padding: 5px;
  font-size: 14px;
}
.create-indent .address .address-detail .icon-address {
  color: #666;
  float: left;
}
.create-indent .address .address-detail p {
  padding-left: 15px;
}
.create-indent .skuinfo {
  background-color: #fff;
  margin-top: 10px;
  padding: 10px;
  height: 120px;
}
.create-indent .skuinfo img {
  float: left;
  width: 100px;
  height: 100px;
}
.create-indent .skuinfo .skuinfo-body {
  padding-left: 110px;
  font-family: PingFangSC-Medium, sans-serif;
}
.create-indent .skuinfo .skuinfo-body .title {
  font-size: 16px;
  color: #333;
  height: 50px;
  line-height: 25px;
}
.create-indent .skuinfo .skuinfo-body .detail {
  color: #666;
  font-size: 14px;
  height: 30px;
  line-height: 15px;
}
.create-indent .skuinfo .skuinfo-body .price-num {
  height: 30px;
  line-height: 30px;
}
.create-indent .skuinfo .skuinfo-body .price-num .price {
  float: left;
  color: red;
  font-size: 14px;
}
.create-indent .skuinfo .skuinfo-body .price-num .price span {
  font-size: 20px;
}
.create-indent .skuinfo .skuinfo-body .price-num .num {
  float: right;
  color: red;
  font-size: 18px;
}
.create-indent .skuinfo .skuinfo-body .price-num .num i {
  margin-right: 10px;
  font-size: 12px;
  color: #666;
}
.create-indent .userInfo {
  margin-top: 20px;
  padding-top: 5px;
  background-color: #fff;
}
.create-indent .userInfo .weui-textarea {
  font-size: 14px;
}
.create-indent .type-list {
  height: auto;
  margin-top: 20px;
  padding: 5px;
  background-color: #fff;
}
.create-indent .type-list .type-pay {
  width: 100%;
  height: 30px;
}
.create-indent .type-list .type-pay .pay-p {
  float: left;
  margin-left: 10px;
  color: #666;
  font-size: 14px;
  line-height: 30px;
}
.create-indent .type-list .type-pay .wechat {
  float: left;
  margin-left: 10px;
  width: 30px;
  height: 30px;
}
.create-indent .type-list .type-pay .check {
  float: right;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  margin-top: 5px;
}
.create-indent footer {
  position: fixed;
  z-index: 99;
  bottom: 0;
  left: 0;
  right: 0;
  height: 58px;
  background-color: #fff;
  font-family: PingFangSC-Semibold, sans-serif;
}
.create-indent footer .pay-li {
  float: left;
  width: 60%;
  line-height: 58px;
  font-size: 12px;
}
.create-indent footer .pay-li .price {
  color: #d81e06;
  font-size: 20px;
}
.create-indent footer .buy-li {
  float: left;
  width: 40%;
  height: 58px;
  line-height: 58px;
  text-align: center;
  background-color: #d81e06;
  color: #fff;
  font-size: 18px;
}



