















































.personal {
    margin-bottom: 100px;
  .portait {
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
    top: 0;
    height: 200px;
    background-color: #343434;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    > div {
      text-align: center;
      img {
        height: 80px;
        width: 80px;
        border-radius: 100%;
        border: 1px solid #666;
      }
      p {
        color: #bdb0b5;
        margin-top: 8px;
      }
    }
  }
  .flex {
    display: flex;
    display: -webkit-flex;
    justify-content: space-around;
    align-items: center;
  }
  .order-grid {
      margin-top: 200px;
        color: #666;
        font-size: 14px;
        .order-img {
            width: 20px;
            display: block;
            margin-top: 3px;
            margin-right: 5px;
        }
  }
  .btns {
      margin-top: 100px;
      height: 50px;
      width: 100%;
      text-align: center;

      .btn {
          position: relative;
          background-color: #ffffff;
          width: 80%;
          height: 100%;
          line-height: 50px;
          margin-left: 10%;
          color: #d81e06;
          border: 1px solid #d81e06;
          border-radius: 10px;
      }
  }
}



